import p5 from 'p5';

export const cachedSlider = p5.prototype.cachedSlider = function cachedSlider(name, min, max, defaultValue) {
  if (!this.__state) {
    this.__state = {};
  }
  this.__state[name] = this.createSlider(min, max, this.__state[name]?.value() ?? defaultValue);
  this.__state[name].addClass("e-range");
  this.__state[name].style('width', '80px');
  this.__state[name].style('margin-top', '8px');
  this.__state[name].style('background', 'blue');
  return this.__state[name];
};